
import { defineComponent } from "vue";
import { _serverBase } from "../consts";
export default defineComponent({
	name: "Profile",
	data() {
		return {
			user: {},
		};
	},
	created() {
		fetch(_serverBase + "/api/v1/profile")
			.then((data) => data.json())
			.then((res) => (this.user = res));
	},
});
